import React from 'react';
import {Motion, spring} from 'react-motion';
import range from 'lodash.range';
import './Demo.css'

function reinsert(arr, from, to) {
  const _arr = arr.slice(0);
  const val = _arr[from];
  _arr.splice(from, 1);
  _arr.splice(to, 0, val);
  return _arr;
}

function clamp(n, min, max) {
  return Math.max(Math.min(n, max), min);
}

const springConfig = {stiffness: 300, damping: 50};
const itemsCount = 50;

export default class Demo extends React.Component {
  constructor(props) {
    super(props);
    this.containerElement = null;
    this.state = {
      topDeltaY: 0,
      mouseY: 0,
      isPressed: false,
      originalPosOfLastPressed: 0,
      order: range(itemsCount),
    };
  };

  componentDidMount() {
    // window.addEventListener('scroll', this.handleScroll, {passive:false});
    window.addEventListener('contextmenu', this.handleScroll, {passive:false});
    window.addEventListener('touchmove', this.handleTouchMove, {passive:true});
    window.addEventListener('touchend', this.handleMouseUp, {passive:false});
    window.addEventListener('mousemove', this.handleMouseMove, {passive:false});
    window.addEventListener('mouseup', this.handleMouseUp, {passive:false});
  };

  handleTouchStart = (key, pressLocation, e) => {
    this.mmw(key, pressLocation, e.touches[0]);
  };

  handleContextMenu = (key, pressLocation, e) => {
    this.handleMouseDown(key, pressLocation, e.pageY);
  };

  handleScroll = (e) => {
    e.preventDefault();
    e.stopPropagation();
    // this.handleMouseMove(e.touches[0]);
  };

  handleTouchMove = (e) => {
    if (this.canStart) {
      e.preventDefault();
      e.stopPropagation();
      this.handleMouseMove(e.touches[0]);
    }
  };

  handleMouseDown = (pos, pressY, {pageY}) => {
    this.setState({
      topDeltaY: pageY - pressY,
      mouseY: pressY,
      isPressed: true,
      originalPosOfLastPressed: pos,
    });
  };
  timeout = null;
  canStart = false;
  mmw = (pos, pressY, {pageY}) => {
    this.timeout = setTimeout(() => {
      this.canStart=true;
      this.handleMouseDown(pos, pressY, {pageY})
    }, 1000);
  }

  autoscroll = false;
  lastPageY = 0;
  handleMouseMove = ({screenY: pageY}) => {
    const {isPressed, topDeltaY, order, originalPosOfLastPressed} = this.state;
    if (isPressed) {
      let mouseY = pageY - topDeltaY;
      console.log(mouseY, pageY, topDeltaY);
      if (pageY > 300) {
        this.containerElement.scrollBy(0,10);
        mouseY = mouseY + 10;
        this.autoscroll=true;
        this.lastPageY = pageY;
      }
      // else if (pageY < 200)
      //   this.containerElement.scrollBy({top:-100, behavior:'smooth'});
      else
          this.containerElement.scrollTop = 0;
      const currentRow = clamp(Math.round((mouseY + this.containerElement.scrollTop) / 50), 0, itemsCount - 1);
      let newOrder = order;

      if (currentRow !== order.indexOf(originalPosOfLastPressed)){
        newOrder = reinsert(order, order.indexOf(originalPosOfLastPressed), currentRow);
      }

      this.setState({mouseY: mouseY, order: newOrder});
    }
    else {
      // this.containerElement.scrollTop = this.containerElement.scrollTop + topDeltaY;
    }
  };

  handleMouseUp = () => {
    clearTimeout(this.timeout);
    this.canStart=false;
    this.autoscroll = false;
    this.setState({isPressed: false, topDeltaY: 0});
  };

  componentDidUpdate() {
    if (this.autoscroll)
      setTimeout(() => this.handleMouseMove({pageY: this.lastPageY}), 50);
  }

  setContainerElement = el => {
    this.containerElement = el;
    // this.containerElement.addEventListener('scroll', this.handleScroll, {passive:false});
  }
  render() {
    const {mouseY, isPressed, originalPosOfLastPressed, order} = this.state;
    const offset = this.containerElement ? this.containerElement.scrollTop : 0;
    return (
      <div ref={this.setContainerElement} className="demo8">
        {range(itemsCount).map(i => {
          const style = originalPosOfLastPressed === i && isPressed
            ? {
                scale: spring(1.0, springConfig),
                shadow: spring(12, springConfig),
                y: mouseY + offset,
              }
            : {
                scale: spring(1, springConfig),
                shadow: spring(1, springConfig),
                y: spring(order.indexOf(i) * 50 , springConfig),
              };
          return (
            <Motion style={style} key={i}>
              {({scale, shadow, y}) =>
                <div
                  onMouseDown={this.mmw.bind(null, i, y)}
                  onTouchStart={this.handleTouchStart.bind(null, i, y)}
                  className="demo8-item"
                  style={{
                    boxShadow: `rgba(0, 0, 0, 0.2) 0px ${shadow}px ${2 * shadow}px 0px`,
                    transform: `translate3d(0, ${y}px, 0) scale(${scale})`,
                    WebkitTransform: `translate3d(0, ${y}px, 0) scale(${scale})`,
                    zIndex: i === originalPosOfLastPressed ? 99 : i,
                  }}>
                  {order.indexOf(i) + 1}
                </div>
              }
            </Motion>
          );
        })}
      </div>
    );
  };
}